import {home, common} from '@/pages'
export default {
  path: '/',
  component: common.Index,
  children: [
    {path: '', name: '', component: home.Home},
    {path: 'untodo', name: '', component: home.Untodo},
    {path: 'havedo', name: '', component: home.Havedo},
    {path: 'todo', name: '', component: home.Todo},
    {path: 'discussion', name: '', component: home.Discussion},
    {path: 'Announcement', name: '', component: home.Announcement},
    {path: 'Documentation', name: '', component: common.Documentation},
  ]
}



