/* API */
import ajax from '../request'
export default {
  /* 1.*/
  /**
   * 
   * @param  {Object} data 
   * @param  {Function} fn 
   */
  getModuleList(data, fn) {
    ajax.call(this, 'get', '/system/sysSetting/moduleManage', data, fn);
  },
  /**
   * 
   * @param  {Object} data 
   * @param  {Function} fn 
   */
  removeModule(data, fn) {
    ajax.call(this, 'get', '/system/sysSetting/removeModule', data, fn);
  },
  /**
   * 
   * @param  {Object} data 
   * @param  {Function} fn 
   */
  removeModuleBatch(data, fn) {
    ajax.call(this, 'get', '/system/sysSetting/removeModuleBatch', data, fn);
  },
  /**
   * /
   * @param  {Object} data 
   * @param  {Function} fn 
   */
  editModule(data, fn) {
    ajax.call(this, 'get', '/system/sysSetting/editModule', data, fn);
  },
  
}



