// requestAnimationFrame
if (!window.requestAnimationFrame) {
  requestAnimationFrame = function (fn) {
    setTimeout(fn, 17);
  };
}

//backTop
/**
 * js
 * @param {number}    A         
 * @param {number}    B          ,0
 * @param {number}    rate       ,2
 * @param {function}  callback  valueisEnding
 * */
export default function (A, B, rate, callback) {
  if (A == B || typeof A != 'number') {
    return;
  }
  B = B || 0;
  rate = rate || 2;

  const step = function () {
    A = A + (B - A) / rate;

    if (A < 1) {
      callback(B, true);
      return;
    }
    callback(A, false);
    requestAnimationFrame(step);
  };
  step();
};



