import {system, common} from '@/pages'
export default {
  path: '/system',
  component: common.Index,
  name: '',
  children: [
    {
      path: 'sysSetting',
      name: '',
      component: common.Blank,
      children: [
        {
          path: 'ModuleManage',
          name: '',
          component: system.sysSetting.ModuleManage
        }
      ]
    },
  ]
}



