import Vue from 'vue'
/*  */
Vue.config.productionTip = false

/* element-ui */
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-default/index.css'
Vue.use(ElementUI)

/* css */
import '@/assets/style/index.less'

/*  */
import NProgress from 'nprogress';
import 'nprogress/nprogress.css';

/*  */
import router from './router'

/* vuex store */
import store from './store';

/* mock */
import Mock from './mock'
Mock.init();

/*  */
import App from './App'

import {scroll} from '@/util'

/*  */
router.beforeEach((to, from, next) => {
  if (!store.state.user.userinfo.token && to.path.toLocaleLowerCase() !== '/signin') {
    store.dispatch('remove_userinfo');
    next('/signin');
  } else {
    if (store.state.user.userinfo.token && to.path.toLocaleLowerCase() === '/signin') {
      next({
        path: '/'
      });
    } else {
      NProgress.start();
      next();
    }
  }
})
/*  */
router.afterEach(() => {
  NProgress.done();
  /*  */
  store.dispatch('hide_leftNav');
  /* body */
  scroll.inchingScroll(document.body.scrollTop, 0, 4, function (value) {
    document.body.scrollTop = value;
  })
});

new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: {App}
})



