/* API */
import ajax from '../request'
export default {
  /**
   * 
   * @param {object}    data          
   * @param {string}    data.username 
   * @param {string}    data.password 
   * @param {function}  fn            
   * @param {Function}  errFn         
   */
  login(data, fn, errFn) {
    ajax.call(this, 'post', 'login', data, fn, true, errFn);
  },
  
  /**
   * 
   * @param  {object}   data 
   * @param {string} data.old_password 
   * @param {string} data.password 
   * @param {string} data.password_confirm 
   * @param  {Function} fn   
   */
  updPass(data, fn) {
    ajax.call(this, 'post', '/User/updatePass', data, fn);
  },
  /**
   * 
   * @param  {Function} fn 
   */
  getUnreadNotice(fn) {
    ajax.call(this, 'get', 'unreadNotice.json', {}, fn);
  },
  /**
   * 
   * @param  {Function} fn 
   */
  getLeftNav(fn) {
    ajax.call(this, 'get', 'leftNav.json', {}, fn);
  },
}



