// 
const gbs = {
    // /config/index.js -> proxyTable
    /*
     *  proxyTable: {
     *    '/static/data/': {
     *      target: 'http://xxxxxx.com',
     *      changeOrigin: true,
     *      pathRewrite: {
     *        '^/static/data/': ''
     *      }
     *    }
     *  },
     * */
    //host: 'http://192.168.17.50:808',
    host: '/static/data/',
    // key
    db_prefix: 'sx_admin_',
  },

  // 
  cbs = {
    /**
     * ajax200
     * @param  {object} err 
     */
    statusError(err) {
      if (err.status !== 404) {
        this.$message({
          showClose: true,
          message: '' + err.message,
          type: 'error'
        });
      } else {
        this.$store.dispatch('remove_userinfo').then(() => {
          this.$alert(err.status + ',' + err.message + '', '', {
            confirmButtonText: '',
            callback: action => {
              this.$router.push('/login');
            }
          });
        });
      }
    },

    /**
     * ajax
     */
    requestError(err, url) {
      this.$message({
        showClose: true,
        message: '(' + err.response.status + ')' + url + ',' + err.response.statusText,
        type: 'error'
      });
    }
  };

export {gbs, cbs}



