<template>
  <div>
    <!--  -->
    <el-form :inline="true" :model='para'>
      <el-form-item>
        <el-input placeholder="" size="small" v-model='para.name' @keyup.native.enter="getLists"></el-input>
      </el-form-item>
      <el-form-item>
        <el-select v-model="para.type" size="small" placeholder="">
          <el-option
            v-for="item in para.options"
            :label="item.label"
            :value="item.value"
            :key="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" size="small" @click='getLists'></el-button>
        <el-button type="primary" size="small" @click='resetQuery'></el-button>
      </el-form-item>
    </el-form>
    
    <!--  -->
    <el-button-group>
      <el-button type="info" icon="plus" @click="handleEdit"></el-button>
      <el-button type="danger" icon="delete" @click="handleDeBatch" :disabled="multipleSelection.length <1">
      </el-button>
    </el-button-group>
    
    <!--  -->
    <el-table
      :data="moduleList"
      border
      stripe
      tooltip-effect="dark"
      v-loading="para.listLoading"
      @selection-change="handleSelectionChange"
      style="width: 100%">
      <el-table-column
        type="selection"
        width="50">
      </el-table-column>
      <el-table-column
        prop="name"
        label=""
        align="center"
        min-width="120">
      </el-table-column>
      <el-table-column
        prop="path"
        label=""
        header-align="center"
        min-width="150">
      </el-table-column>
      <el-table-column
        prop="accessName"
        label=""
        align="center"
        min-width="120">
      </el-table-column>
      <el-table-column
        prop="delimitName"
        label=""
        align="center"
        min-width="120">
      </el-table-column>
      <el-table-column
        prop="show"
        label=""
        align="center"
        min-width="85">
        <template scope="scope">
          <i class="fa" :class="scope.row.show == 1 ? 'fa-check' : 'fa-times'"></i>
        </template>
      </el-table-column>
      <el-table-column
        prop="sort"
        min-width="90"
        align="center"
        label="">
      </el-table-column>
      <el-table-column
        fixed="right"
        align="center"
        label=""
        min-width="120">
        <template scope="scope">
          <el-button @click="handleEdit(scope.$index, scope.row)" size="small" icon="edit"></el-button>
          <el-button @click="handleDel(scope.$index, scope.row)" type="danger" size="small" icon="delete"></el-button>
        </template>
      </el-table-column>
    </el-table>
    
    <!--  -->
    <el-pagination
      @current-change="handleCurrentChange"
      :current-page="para.page"
      :page-size="para.pageSize"
      :layout="layout"
      :total="para.total"
      class="text-center m-t-sm">
    </el-pagination>
    
    <!-- / -->
    <el-dialog :title="editData.id ? '' : ''" size="large" v-model="editVisible" top="30px"
               :close-on-click-modal="false">
      <el-form :model="editData" ref="editForm" :label-width="formLabelWidth" :rules="editFormRules">
        <el-row :gutter="10">
          <el-col :xs="24" :sm="12" :lg="8">
            <el-form-item label="" prop="name">
              <el-input v-model="editData.name" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :xs="24" :sm="12" :lg="8">
            <el-form-item label="">
              <el-select v-model="editData.belong">
                <el-option
                  v-for="item in para.options"
                  :label="item.label"
                  :value="item.value"
                  :key="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :xs="24" :sm="12" :lg="8">
            <el-form-item label="">
              <el-input v-model="editData.path" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :xs="12" :sm="12" :lg="8">
            <el-form-item label="">
              <el-radio-group v-model="editData.show">
                <el-radio :label="1"></el-radio>
                <el-radio :label="0"></el-radio>
              </el-radio-group>
            </el-form-item>
          </el-col>
          <el-col :xs="12" :sm="12" :lg="8">
            <el-form-item label="">
              <el-input v-model="editData.sort" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :xs="24" :sm="12" :lg="8">
            <el-form-item label="">
              <el-input v-model="editData.icon" auto-complete="off">
                <template slot="append"><i class="fa" :class="editData.icon"></i></template>
              </el-input>
            </el-form-item>
          </el-col>
          <el-col :xs="24" :sm="12" :lg="12">
            <el-form-item label="">
              <el-input
                type="textarea"
                autosize
                readonly
                placeholder=""
                v-model="editData.accessName"
                @click.native="choosePerson">
              </el-input>
            </el-form-item>
          </el-col>
          <el-col :xs="24" :sm="12" :lg="12">
            <el-form-item label="">
              <el-input
                type="textarea"
                autosize
                readonly
                placeholder=""
                v-model="editData.accessDept"
                @click.native="chooseDept">
              </el-input>
            </el-form-item>
          </el-col>
          <el-col :xs="24" :sm="12" :lg="12">
            <el-form-item label="">
              <el-input
                type="textarea"
                autosize
                readonly
                placeholder=""
                v-model="editData.delimitName"
                @click.native="choosePerson">
              </el-input>
            </el-form-item>
          </el-col>
          <el-col :xs="24" :sm="12" :lg="12">
            <el-form-item label="">
              <el-input
                type="textarea"
                autosize
                readonly
                placeholder=""
                v-model="editData.delimitDept"
                @click.native="chooseDept">
              </el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editVisible = false"> </el-button>
        <el-button type="primary" @click="handleSubmit" :loading="para.editLoading"> </el-button>
      </div>
    </el-dialog>
    
    <!--  -->
    <el-dialog title="" v-model="choosePersonVisible" top="30px" size="tiny">
      <el-input
        placeholder=""
        icon="search"
        v-model="filterText">
      </el-input>
      <el-tree
        :data="allUser"
        :props="defaultProps"
        show-checkbox
        node-key="id"
        highlight-current
        class="scrollable m-t-sm"
        style="max-height: 300px;"
        ref="allUser"
        :filter-node-method="filterNode"
        v-loading="choosePersonLoading"></el-tree>
      <div slot="footer" class="dialog-footer">
        <el-button size="mini" @click="selectAll" type="success" class="pull-left" :disabled="!!filterText">
        </el-button>
        <el-button size="mini" @click="cancelAll" type="danger" class="pull-left"></el-button>
        <el-button @click="choosePersonVisible = false"> </el-button>
        <el-button type="primary" @click="getChecked"> </el-button>
      </div>
    </el-dialog>
    
    <!--  -->
    <el-dialog title="" v-model="chooseDeptVisible" top="30px">
      <el-tree
        :data="allDept"
        :props="defaultProps"
        show-checkbox
        node-key="id"
        highlight-current
        class="scrollable m-t-sm"
        style="max-height: 300px;"></el-tree>
      <div slot="footer" class="dialog-footer">
        <el-button @click="chooseDeptVisible = false"> </el-button>
        <el-button type="primary" @click="chooseDeptVisible = false"> </el-button>
      </div>
    </el-dialog>
  
  </div>

</template>

<script>
  import {systemApi, getterApi} from '@/config'
  export default{
    name: 'ModuleManage',
    data () {
      return {
        // ,
        para: {
          name: '',
          type: '',
          page: 1,
          total: 0,
          pageSize: 10,
          options: [
            {
              value: '1',
              label: ''
            },
            {
              value: '2',
              label: ''
            }, {
              value: '3',
              label: ''
            }
          ],
          editLoading: false,
          listLoading: false
        },
        
        /*  */
        multipleSelection: [],
        
        /*  */
        moduleList: [],
        
        /* , */
        editVisible: false,
        editData: {
          id: 0,
          name: '',
          belong: '',
          path: '',
          show: 1,
          sort: 1,
          icon: 'fa-circle-o',
          accessName: '',
          accessUid: [],
          accessDept: '',
          accessPid: [],
          delimitName: '',
          delimitUid: [],
          delimitDept: '',
          delimitPid: [],
        },
        editFormRules: {
          name: {
            required: true,
            message: '',
            trigger: 'blur'
          }
        },
        
        formLabelWidth: '85px',
        
        /*  */
        choosePersonVisible: false,
        choosePersonLoading: false,
        filterText: '',
        allUser: [],
        
        /*  */
        chooseDeptVisible: false,
        
        allDept: [],
        
        defaultProps: {
          label: 'name'
        },
        
        layout: this.$store.state.user.sys == 'pc' ? 'prev, pager, next, jumper, total' : 'prev, pager, next'
      }
    },
    props: [],
    components: {},
    watch: {
      filterText(val) {
        this.$refs.allUser.filter(val);
      }
    },
    methods: {
      // 
      getLists(){
        this.para.listLoading = true;
        let para = {
          page: this.para.page,
          name: this.para.name,
          type: this.para.type,
          pageSize: this.para.pageSize
        };
        systemApi.getModuleList.call(this, para, data => {
          this.moduleList = data.moduleList;
          this.para.total = data.total;
          this.para.listLoading = false;
        })
      },
      
      // 
      handleCurrentChange(val) {
        this.para.page = val;
        this.getLists();
      },
      
      // 
      resetQuery(){
        Object.assign(this.para, {name: '', type: ''})
      },
      
      //  
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      
      //  
      handleDel(index, row) {
        this.$confirm('?', '', {
          type: 'warning'
        }).then(() => {
          let para = {id: row.id};
          systemApi.removeModule.call(this, para, data => {
            this.getLists();
            this.$message({
              message: data.message,
              type: 'success'
            });
          })
        })
      },
      
      // 
      handleDeBatch(){
        let ids = this.multipleSelection.map(item => item.id);
        this.$confirm('?', '', {
          type: 'warning'
        })
          .then(() => {
            let para = {
              ids: ids
            };
            systemApi.removeModuleBatch.call(this, para, data => {
              this.getLists();
              this.$message({
                message: data.message,
                type: 'success'
              });
            })
          })
      },
      
      //  ,
      handleEdit(index, row){
        this.editVisible = true;
        /* Object.assign(target, ...sources) */
        /*  */
        Object.assign(this.editData, {
          id: 0,
          name: '',
          belong: '',
          path: '',
          show: 1,
          sort: 1,
          icon: 'fa-circle-o',
          accessName: '',
          accessUid: [],
          accessDept: '',
          accessPid: [],
          delimitName: '',
          delimitUid: [],
          delimitDept: '',
          delimitPid: [],
        }, row);
      },
      
      // 
      handleSubmit(){
        this.$refs['editForm'].validate((valid) => {
          if (valid) {
            this.$confirm('', '', {})
              .then(() => {
                this.para.editLoading = true;
                let para = Object.assign({}, this.editData);
                systemApi.editModule.call(this, para, data => {
                  this.para.editLoading = this.editVisible = false;
                  this.$message({
                    message: data.message,
                    type: 'success'
                  });
                  this.getLists();
                });
              });
          }
        });
      },
      
      // 
      choosePerson(){
        this.choosePersonVisible = true;
        this.filterText = '';
        this.getAllUser();
      },
      
      // 
      getAllUser(){
        this.choosePersonLoading = true;
        getterApi.getAllUser.call(this, data => {
          this.allUser = data.userList;
          this.choosePersonLoading = false;
          this.editData.accessUid[0] == '000' ? this.selectAll() : this.$refs.allUser.setCheckedKeys(this.editData.accessUid)
        })
      },
      
      // 
      filterNode(value, data) {
        if (!value) return true;
        return data.name.indexOf(value) !== -1;
      },
      
      // 
      getChecked() {
        let selectList = this.$refs.allUser.getCheckedNodes();
        this.editData.accessName = selectList.length === this.allUser.length ? '' : selectList.map(item => item.name).toString();
        this.editData.accessUid = selectList.length === this.allUser.length ? ['000'] : selectList.map(item => item.id);
        this.choosePersonVisible = false;
      },
      
      // 
      selectAll(){
        let list = this.allUser.map(item => item.id);
        this.$refs.allUser.setCheckedKeys(list)
      },
      
      // 
      cancelAll(){
        this.$refs.allUser.setCheckedKeys([])
      },
      
      // 
      chooseDept(){
        this.chooseDeptVisible = true;
      },
      
    },
    beforeMount () {
      this.getLists()
    },
    mounted(){
      
    }
  }
</script>
<style>
  .el-table .cell {
    white-space: nowrap;
  }
</style>



